.contacts-middle {
  display: flex;
  flex-direction: column;
  float: right;
}

.contacts-border {
  background: #ffffff;
  border: 1px solid #000000;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  padding-left: 55px;
  width: 500px;
  margin-right: 135px;
}

.contacts-info {
  font-size: 18px;
  line-height: 23px;
  padding-top: 45px;
  padding-bottom: 45px;
  min-height: 425px;
  margin-top: 30px;
  margin-bottom: 20px;
}

.contacts-info dl {
  margin: 0;
}

.contacts-info dt {
  font-weight: bold;
  padding-bottom: 7px;
}

.contacts-info dd {
  margin: 0;
  margin-bottom: 20px;
}

.contacts-info a {
  text-decoration: none;
  color: #000;
  line-height: 25px;
}

.subscribe {
  margin-bottom: 25px;
}

.social-btn {
  width: 50px;
  height: 50px;
  background-repeat: no-repeat;
  display: block;
  font-size: 0;
  margin-bottom: 5px;
}

.subscribe .btn-in {
  background-image: url("../components/img/instagramdark.svg");
}

.subscribe .btn-vk {
  background-image: url("../components/img/vkdark.svg");
  margin-left: 70px;
  margin-top: -55px;
}

