nav ul {
    margin-top: 60px;
    margin-left: 20px;
    padding: 0;
}

nav ul li {
    list-style: none;
    margin-bottom: 20px;
}

nav ul li a {
    text-decoration: none;
    color: white;
    text-transform: uppercase;
    font-size: 14px;
}

.navOpen {
    height: 100%;
    width: 300px;
    background-color: #201B1B;
    position: fixed;
    display: block;
}

.navClose {
    display: none;
}