.modal {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    overflow: auto;
}

.closeModal {
    display: block;
    width: 25px;
    height: 25px;
    margin-right: 10px;
    margin-top: 10px;
    margin-left: auto;

}

.closeModal:hover {
    cursor: pointer;
}

.type-modal-wrapper {
    margin-left: 50px;
}

.type-modal {
    color: white;
    font-size: 16px;
    text-transform: uppercase;
    margin-right: 20px;
}

.type-modal:hover {
    border-bottom: 2px solid white;
    cursor: pointer;
}

.active-type {
    border-bottom: 2px solid white;
}

.modal-form__input {
    width: 300px;
    border: none;
    border-bottom: 2px solid #fff;
    background: none;
    font-size: 14px;
    margin-top: 35px;
    color: #fff;
}

.modal-form__input:focus {
    outline: none;
}

/* Стили формы входа */

.modal-login-wrapper {
    position: absolute;
    width: 400px;
    height: 500px;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    background-color: black;
    border-radius: 10px;

}

.login-form {
    margin: 100px 50px 20px 50px;
}


.login-form__btn {
    margin-top: 70px;
    margin-bottom: 40px;
    background-color: #fff;
    width: 300px;
    height: 40px;
    border: none;
    border-radius: 40px;
    font-size: 18px;
}

.login-form__btn:focus {
    outline: none;
}

.login-form__btn:hover {
    cursor: pointer;
}

.modal-login__password_recovery {
    display: block;
    width: 100%;
    margin-top: 15px;
    text-decoration: none;
    font-size: 14px;
    color: #fff;
    text-align: center;
}

.modal-login__password_recovery:hover{
    cursor: pointer;
}

/* Стили формы регистрации */

.modal-logout-wrapper {
    position: absolute;
    width: 900px;
    height: 600px;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    background-color: black;

}

.select {
    width: 300px;
}

.registration-form {
    margin: 40px 70px 20px 70px;
}

.registration-wrapper {
    display: flex;
}

.registration-column {
    width: 300px;
    margin-right: 160px;
}

.registration-parent-wraper {
    margin-top: 60px;
}

.checkAge {
    color: #fff;
}

.registration-parent-input-wraper {
    display: flex;
}

.registration-btn {
    margin-top: 70px;
    margin-bottom: 40px;
    background-color: #fff;
    width: 300px;
    height: 40px;
    border: none;
    border-radius: 40px;
    font-size: 18px;
}

.registration-lbl {
    color: #fff;
}