.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

.footer {
  width: inherit;
  padding-top: 55px;
  padding-bottom: 20px;
  padding-left: 35px;
  padding-right: 35px;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  color: #ffffff;
  background-color: #000000;
  min-height: 180px;
}

.footer-social {
  float: left;
  width: 128px;
  margin-top: -20px;
}

.social-btn {
  width: 50px;
  height: 50px;
  background-repeat: no-repeat;
  display: block;
  font-size: 0;
}

.btn-in {
  background-image: url("img/instagram.svg");
}

.btn-vk {
  background-image: url("img/vk.svg");
  margin-left: 70px;
  margin-top: -55px;
}

.footer-contacts {
  float: right;
  width: 240px;
}

.footer-contacts a {
  color: #fff;
  text-decoration: none;
  float: right;
  margin-bottom: 15px;
}

.footer-copyright {
  margin-top: 150px;
  text-align: center;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}
